#home .gradient {
  background: -moz-linear-gradient(
    45deg,

    rgba(100,243,140, 0.7),
    rgba(247,157,0, 0.7) 100%
  );
  background: -webkit-linear-gradient(
    45deg,

    rgba(100,243,140, 0.7),
    rgba(247,157,0, 0.7) 100%
  );
  background: -webkit-gradient(
    linear,
    45deg,
    from(rgba(100,243,140, 0.7)),
    to(rgba(247,157,0, 0.7))
  );
  background: -o-linear-gradient(
    45deg,
    rgba(100,243,140, 0.7),
    rgba(247,157,0, 0.7) 100%
  );
  background: linear-gradient(
    45deg,
    rgba(100,243,140, 0.7),
    rgba(247,157,0, 0.7) 100%
  );
}

.image-2{
  opacity: 0.5;
}

.logo{
  height: 40px;
}

.menu{
  display: flex;
  justify-content: center;
  padding: 0 10px;
}

#home .view {
  background-image: url("../../assets/spoon.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 100vh;
}

#home h6 {
  line-height: 1.7;
}

@media only screen and (max-width: 400px) {
  #home .animated img {
    max-height: 50vh;
    transform: translateX(-50%);
    left: 50%;
    position: absolute;
  }
}
@media only screen and (max-width: 767px) {
  #home .animated img {
    max-height: 35vh;
    transform: translateX(-50%);
    left: 50%;
  }
}

@media (max-width: 767px) and (orientation: landscape) {
  #home .animated img {
    max-height: 50vh;
    transform: translateX(-50%);
    left: 50%;
    position: relative;
  }
}
@media (max-width: 1000px) and (orientation: landscape) {
  #home .view {
    min-height: 150vh;
  }
}